<template>  
    <svg viewBox="0 0 100 100" width="100%" height="100%" preserveAspectRatio="none">
      <rect v-for='i in data.length' :key='i'
        :x='100/data.length*(i-1) + +gap/2' :height="(percentages[i-1] || 2)"
        y=0 :width='100/data.length - +gap' :fill="colors[i-1]">
        <animate v-if="isAnimated" attributeName="height" from=0 :to='percentages[i-1]+5' dur=2s fill="freeze" />
      </rect>
    </svg>
</template>

<script>
export default {
  name: 'histogram',
  props: {
    data: { type: Array, required: true },
    colors: { type: Array, required: true },
    isAnimated: { type: Boolean, required: false, default: false },
    gap: { type: Number, required: false, default: 0 },
  },
  computed: {
    percentages() { return this.data.map(e => Math.ceil(e / this.data.max() * 100)) },
  },
}
</script>

<style lang="scss" scoped>
svg { transform: scale(1,-1) }
rect {
  stroke-linecap: round;
}
