<template>
  <svg viewBox="0 0 210 210" width="100%" height="100%">
    <polygon :points='points' transform="translate(105, 105)" :stroke="color" :fill="color+'5'"></polygon>
    <circle class="wire" v-for='i in 5' :key='i' cx=105 cy=105 :r='20*i'></circle>
    <g v-for='i in N' :key='i' transform="translate(105, 105)">
      <line class="wire" x1=0 y1=0 :x2='X[i-1]' :y2='Y[i-1]'></line>
      <!--<circle class="label-circle" :cx='X[i-1]' :cy='Y[i-1]' r=7></circle>-->
      <text :x='X[i-1]' :y='Y[i-1]'>{{ i }}</text>
    </g>
  </svg>
  <!--<span v-for='i in data.length' :key='i' >
    <span class='centered label-text' :style='{left: 50+X[i-1]/2+"%", top: 50+Y[i-1]/2+"%"}'>{{i}}</span>
  </span>-->
</template>

<script>
export default {
  props: {
    data: Array,
    color: String,
  },
  computed: {
    N() { return this.data.length },
    fi() { 
      let arr=[]
      for(let i=0; i<this.N; i++) arr.push(-2*Math.PI/this.N*i) 
      return arr 
    },
    X() { return this.fi.map(e => 100*Math.cos(e)) },
    Y() { return this.fi.map(e => 100*Math.sin(e)) },
    x() { return this.X.map((e,i) => e*this.data[i]/100) },
    y() { return this.Y.map((e,i) => e*this.data[i]/100) },
    points() { return this.x.reduce((acc,e,i) => acc + e + ' ' + this.y[i] + ',','').slice(0, -1)

    }
  }
}
</script>

<style lang="scss" scoped>
.wire {
  fill: none;
  stroke: #0004;
  stroke-width: .5;
}
polygon {
  stroke-width: 1%;
}
text {
  font-size: 1em;
  font-weight: bold;
  text-anchor: middle;
  dominant-baseline: middle;
  //stroke: #fff;
}
