<template>
<main-layout :backgroundImage="`${$cloudinary}main.png`" backgroundOffset="50">

  <template #header>
    <personal-info-header :userinfo="hasData ? `${stats.general?.score ?? 0} / ${stats.general.scoreMax} балів` : `0 / 0 балів`"/>
  </template>

  <template #body>

    <div class="stats-section">

      <div class="stats-card" id="card1">
        <div class="card-name">Загальна статистика</div>
        <div class="chart-wrapper">
          <chart-doughnut v-if="hasData" :data="[stats.general.percentage, 100-stats.general.percentage]" :colors="[mix3(stats.general.percentage),'#0002']">
            <div class="chart-percentage">{{ stats.general.percentage }}%</div>
            <div class="chart-label">правильно</div> 
          </chart-doughnut>
          <chart-doughnut v-else :data="[100]" :colors="['#0002']"><div class="chart-percentage">0%</div></chart-doughnut>
        </div>
        <div class="card-content" v-if="hasData">
          <div class="card-item">
            <div class="chart-label">Набрано балів:</div>
            <div class="chart-percentage">{{ stats.general.score }}</div>
          </div>
          <div class="card-item">
            <div class="chart-label">Розпочато тем:</div>
            <div class="chart-percentage">{{ stats.blocks.length }}</div>
          </div>
          <div class="card-item">
            <div class="chart-label">Вирішено завдань:</div>
            <div class="chart-percentage">{{ stats.general.tasksCompleted }}</div>
          </div>
          <div class="card-item">
            <div class="chart-label">Спроб на завдання:</div>
            <div class="chart-percentage">{{ stats.general.attemptsAvg.toPrecision(2) }}</div>
          </div>
        </div>
        <div v-else class="dummy">Тут невдовзі з'явиться ваша статистика</div>
      </div>

      <div class="stats-card" id="card2">
        <div class="card-name">Статистика за темами</div>
        <div class="chart-wrapper">
          <chart-doughnut  v-if="hasData" :data="blocksDistribution" :colors="blocksDistribution.map((_,i) => mix3(10*i+5))">
            <div class="chart-percentage">{{ Math.round(stats.blocks.map(e => e.percentage).average()) }}%</div>
            <div class="chart-label">медіана</div> 
          </chart-doughnut>
          <chart-doughnut :data="[100]" :colors="['#0002']" v-else><div class="chart-percentage">0%</div></chart-doughnut>
        </div>
        <div class="card-content" v-if="hasData">
          <div class="card-item" v-for="(block, index) in stats.blocks" :key="index">
            {{index + 1}}.&ensp;
            <span class="tag1" :style="{ background: mix3(block.percentage) }">{{ block.percentage }}%</span>&ensp;
            {{ block._id }}
          </div>
        </div>
        <div v-else class="dummy">Тут невдовзі з'явиться ваша статистика</div>
      </div>

      <div class="stats-card" id="card3">
        <div class="card-name">Статистика за розділами</div>
        <div class="chart-wrapper">
          <chart-column v-if="hasData" :data="topicsDistribution" :colors="topicsDistribution.map(e => mix3(e))" :gap='2'/>
          <chart-column v-else :data="[10,20,30]" :colors="['#0002','#0002','#0002']" :gap='2'/>
        </div>
        <div class="card-content" v-if="hasData">
          <div class="card-item" v-for="(topic, index) in stats.topics" :key="index">
            {{index + 1}}.&ensp;
            <span class="tag1" :style="{ background: mix3(topic.percentage) }">{{ topic.percentage }}%</span>&ensp;
            {{ topic._id }}
          </div>
        </div>
        <div v-else class="dummy">Тут невдовзі з'явиться ваша статистика</div>
      </div>

    </div>

  </template>

</main-layout>
</template>

<script>
import mainLayout from '@/layouts/main-layout.vue'
import personalInfoHeader from '@/components/personal-info-header.vue'
import ChartDoughnut from '../components/charts/chart-doughnut.vue'
import ChartRadar from '../components/charts/chart-radar.vue'
import ChartColumn from '../components/charts/chart-column.vue'
export default {
  components: { mainLayout, personalInfoHeader, ChartDoughnut, ChartColumn, ChartRadar },
  computed: {
    stats() { return this.$store.state.stats },
    hasData() { return Boolean(this.stats) },
    blocksDistribution() { return this.stats?.blocks.map(e => e.percentage).bucket(10, 100).map(e => e.length) },
    topicsDistribution() { return this.stats?.topics.map(e => e.percentage) },
  }
}
</script>

<style lang="scss" scoped>
//* {border: 1px solid red}
.stats-section {
  display: flex;
  flex-wrap: wrap;
  gap: 2em;
  & > * { flex: 1 1 25em }
}
.tag1 {
  padding: .15em .45em;
}
.section-name {
  font-size: 1.1em;
  font-weight: bold;
  padding: .75em 2em;
  margin-bottom: 1em;
  border-radius: 4px;
  text-transform: uppercase;
  background-color: rgba(0,0,0,.025)
}
.stats-card {
  margin-right: auto;
  padding: 2.5em 2em;
  box-sizing: border-box;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 2px 16px 5px rgba(0,0,0,.05);
  .card-content { 
    height: 13em 
  }
  .card-name {
    font-family: 'Oswald';
    font-size: 1.25em;
    text-align: center;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
  }
  .chart-wrapper {
    width: 12em;
    height: 12em;
    margin: 2em auto;
  }
  .chart-percentage {
    margin-bottom: 5px;
    font-family: 'Oswald';
    font-size: 2em;
    font-weight: 500;
    text-align: center;
    text-align: center;
  }
  .chart-label {
    color: #000a;
    text-align: center;
    text-transform: uppercase;
    text-align: center;
  }
}
.dummy {
  padding: 1em;
  text-align: center;
  color: #0008;
  font-size: .9em;
}
#card1 {
  .card-item {
    display: inline-block;
    width: calc(50% - 1em);
    margin: 1em .5em;
  }
}

#card2, #card3 {
  .card-content { 
    width: 100%; 
    overflow-y: scroll;
    .card-item + .card-item { border-top: 1px solid #0002; }
  }
  .card-item {
    padding: .6em 0;
    line-height: 1.5em;
    &::first-letter { font-weight: bold }
  }
}

#card3 {
  .chart-wrapper { 
    width: min(15em, 60%);
    padding: 1.5em 0;
    box-sizing: border-box;
  }
}
</style>